import React, { useState } from 'react';
import { useService } from '../service';
import { useAccountInformation } from './account';

export default function UserDetail () {

    const user = useAccountInformation();

    return <div id="user-detail">
        { Boolean( user ) && <>
            <h1></h1>
            <AccountInfoForm />
            <UserInfoForm />
            <OrderDefaultForm />
        </> }
    </div>;
}

function AccountInfoForm () {
    const service = useService();
    const user = useAccountInformation() ?? {};

    const [ isLoading, setLoadingState ] = useState( false );

    const [ userNameInput, setUserNameInputElement ] = useState();
    const [ passwordInput, setPasswordInputElement ] = useState();
    const [ comfirmInput, setPasswordComfirmElement ] = useState();

    const [ inputError, setInputError ] = useState( false );

    function submitHandler ( event ) {
        event.preventDefault();
        let error = 0;

        if ( passwordConfirm.value !== passwordElement.value ) {
            error += 2
        };

        if ( passwordElement.value.length < 8 ) {
            error += 4
        };

        if ( error ) return setInputError( error );

        return ;

        service.register( userNameElement.value, passwordElement.value ).catch( function () {
            setInputError(1);
            setLoadingState( false );
        });
    }
    return <form onSubmit={ submitHandler }>
        <h2></h2>
        <label className="summary-form">
            
            <input type="text" ref={ setUserNameInputElement } placeholder="" title="" defaultValue={ user.userName } required/>
        </label>
        <label className="summary-form">
            
            <input type="password" ref={ setPasswordInputElement } placeholder=" 8 " required/>
        </label>
        <label className="summary-form">
            <input type="password" ref={ setPasswordComfirmElement } placeholder="" required/>
        </label>

        { Boolean( inputError & 1 ) && <span className="error-message"></span> }
        { Boolean( inputError & 2 ) && <span className="error-message"></span> }
        { Boolean( inputError & 4 ) && <span className="error-message"> 8 </span> }

        <button type="submit" disabled={ isLoading }>{ isLoading ? '': '' }</button>
        <button type='reset'></button>
    </form>;
}

function UserInfoForm () {

    const user = useAccountInformation();

    const [ nameInput, setNameInputElement ] = useState();
    const [ emailInput, setEmailInputElement ] = useState();

    function submitHandler ( event ) {
        event.preventDefault();
    }

    return <form onSubmit={ submitHandler }>
        <h2></h2>
        <label className="summary-form">
            
            <input type="text" ref={ setNameInputElement } placeholder="" title="" defaultValue={ user.name }/>
        </label>
        <label className="summary-form">
            
            <input type="email" ref={ setEmailInputElement } placeholder="" pattern="[0-9]" title="" defaultValue={ user.email }/>
        </label>
        <button type='submit'></button>
        <button type='reset'></button>
    </form>;
}

function OrderDefaultForm () {

    const user = useAccountInformation() ?? {};

    const [ nameInput, setNameInputElement ] = useState();
    const [ telInput, setTelInputElement ] = useState();
    const [ AddrInput, setAddrInputElement ] = useState();

    function submitHandler ( event ) {
        event.preventDefault();
    }

    return <form onSubmit={ submitHandler }>
        <h2></h2>
        <p></p>
        <label className="summary-form">
            
            <input type="text" ref={ setNameInputElement } placeholder="" title="" defaultValue={ user.recipient }/>
        </label>
        <label className="summary-form">
            
            <input type="text" ref={ setTelInputElement } placeholder="" pattern="[0-9]" title="" defaultValue={ user.tel }/>
        </label>
        <label className="summary-form">
            
            <input type="text" ref={ setAddrInputElement } placeholder="" title="" defaultValue={ user.address }/>
        </label>
        <button type='submit'></button>
        <button type='reset'></button>
    </form>;
}